import React, { useContext, useEffect, useState } from 'react';
import Fade from 'react-reveal/Fade';
import Tilt from 'react-tilt';
import { Container, Row, Col } from 'react-bootstrap';
import PortfolioContext from '../../context/context';
import Title from '../Title/Title';
import helper from '../../util/help';

const Projects = () => {
  const { projects } = useContext(PortfolioContext);

  const [isDesktop, setIsDesktop] = useState(false);
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    if (window.innerWidth > 769) {
      setIsDesktop(true);
      setIsMobile(false);
    } else {
      setIsMobile(true);
      setIsDesktop(false);
    }
  }, []);

  return (
    <section id="projects">
      <Container>
        <div className="project-wrapper">
          {projects.map((group) => (
            <>
              <Title title={group.groupTitle} />
              {group.data.map((project) => {
                const { title, info, info2, info3, url, repo, img, id } = project;

                return (
                  <Row key={id}>
                    <Col lg={4} sm={12}>
                      <Fade
                        left={isDesktop}
                        bottom={isMobile}
                        duration={1000}
                        delay={500}
                        distance="30px"
                      >
                        <div className="project-wrapper__text">
                          <h3 className="project-wrapper__text-title">
                            {title || 'Project Title'}
                          </h3>
                          <div>
                            {info !== '' ? (
                              <p dangerouslySetInnerHTML={{ __html: helper.formatContent(info) }} />
                            ) : (
                              'Lorem ipsum dolor sit, amet consectetur adipisicing elit. Excepturi neque, ipsa animi maiores repellendu distinctioaperiam earum dolor voluptatum consequatur blanditiis inventore debitis fuga numquam voluptate architecto itaque molestiae.'
                            )}
                            {info2 !== '' ? (
                              <p
                                className="mb-4"
                                dangerouslySetInnerHTML={{ __html: helper.formatContent(info2) }}
                              />
                            ) : (
                              ''
                            )}
                            {info3 !== '' ? (
                              <p
                                className="mb-4"
                                dangerouslySetInnerHTML={{ __html: helper.formatContent(info3) }}
                              />
                            ) : (
                              ''
                            )}
                          </div>
                          <a
                            target="_blank"
                            rel="noopener noreferrer"
                            className="cta-btn cta-btn--hero"
                            href={url || '#!'}
                          >
                            See Live
                          </a>

                          {repo && (
                            <a
                              target="_blank"
                              rel="noopener noreferrer"
                              className="cta-btn text-color-main"
                              href={repo}
                            >
                              Source Code
                            </a>
                          )}
                        </div>
                      </Fade>
                    </Col>
                    <Col lg={8} sm={12}>
                      <Fade
                        right={isDesktop}
                        bottom={isMobile}
                        duration={1000}
                        delay={1000}
                        distance="30px"
                      >
                        <div className="project-wrapper__image">
                          <a
                            href={url || '#!'}
                            target="_blank"
                            aria-label="Project Link"
                            rel="noopener noreferrer"
                          >
                            <Tilt
                              options={{
                                reverse: false,
                                max: 8,
                                perspective: 1000,
                                scale: 1,
                                speed: 300,
                                transition: true,
                                axis: null,
                                reset: true,
                                easing: 'cubic-bezier(.03,.98,.52,.99)',
                              }}
                            >
                              <div data-tilt className="thumbnail rounded">
                                <img alt={title} src={img} />
                              </div>
                            </Tilt>
                          </a>
                        </div>
                      </Fade>
                    </Col>
                  </Row>
                );
              })}
            </>
          ))}
        </div>
      </Container>
    </section>
  );
};

export default Projects;
